"use client"

import { useState } from "react";
import api from "../../lib/axios";
import { useRouter } from "next/navigation";

export default function LoginPage() {
  const [form, setForm] = useState({ email: "", password: "" });
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const handleChange = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
       await fetch("/api/proxy/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });
      router.push("/posts");
    } catch (err) {
      alert(err.response?.data?.message || "Login failed");
    }
    setLoading(false);
  };

  return (
    <div className="flex items-center justify-center min-h-screen">
      <div className="card w-96 bg-base-100 shadow-xl p-6">
        <h2 className="card-title mb-4">Sign In</h2>
        <form onSubmit={handleSubmit} className="flex flex-col gap-3">
          <input type="email" name="email" placeholder="Email" className="input input-bordered" onChange={handleChange} />
          <input type="password" name="password" placeholder="Password" className="input input-bordered" onChange={handleChange} />
          <button type="submit" className={`btn btn-primary ${loading ? "loading" : ""}`}>Login</button>
        </form>
      </div>
    </div>
  );
}
